import React from 'react'
import { Grommet, Button } from 'grommet'
import { CustomizeTheme } from '../styles'

function BackButton(params) {
  return (
    <Grommet theme={CustomizeTheme({
      button: {
        color: 'brand'
      }
    })}>
      <Button label="Back" onClick={params.onClick} />
    </Grommet>
  );
}

export default BackButton